export const text1 =
  "ಮಾಸ್ಕ್ ಧರಿಸದೆ ಇದ್ದರೆ  ರೂ ದಂಡ ಭಾರತೀಯ ರೈಲ್ವೆಕೊರೊನಾ ವೈರಸ್ ಸೋಂಕು ಹರಡುವುದನ್ನು ತಡೆಯಲು ಮುನ್ನೆಚ್ಚರಿಕೆ ಕ್ರಮವಾಗಿ ಭಾರತೀಯ ರೈಲ್ವೆಯು ನಿಲ್ದಾಣಗಳು ರೈಲುಗಳಲ್ಲಿ ಮಾಸ್ಕ್ ಧರಿಸುವ ನಿಯಮ ಉಲ್ಲಂಘಿಸುವ ಜನರಿಗೆ  ರೂಪಾಯಿ ದಂಡ ವಿಧಿಸುವುದಾಗಿ ಪ್ರಕಟಿಸಿದೆ ಉಗುಳುವ ಅಥವಾ ರೀತಿಯ ಕೃತ್ಯಗಳು ಕಂಡುಬಂದರೆ ಆವರಣದ ಸ್ವಚ್ಛತೆಯನ್ನು ಹಾಳುಮಾಡುವ ಚಟುವಟಿಕೆ ಎಂದು ಪರಿಗಣಿಸಿ  ರೂಪಾಯಿ ದಂಡ ವಿಧಿಸುವುದಾಗಿಯೂ ಪ್ರಯಾಣಿಕರಿಗೆ ರೈಲ್ವೆ ಎಚ್ಚರಿಕೆ ನೂತನ ದಂಡ ನಿಯಮಗಳು ತಕ್ಷಣದಿಂದಲೇ ಜಾರಿಗೆ ಬರುತ್ತಿದ್ದು ಮುಂದಿನ ಆದೇಶ ಪ್ರಕಟಿಸುವವರೆಗೂ ಆರು ತಿಂಗಳವರೆಗೆ ಚಾಲ್ತಿಯಲ್ಲಿ ಇರಲಿದೆ ರೈಲುಗಳ ಚಲನೆಯಲ್ಲಿನ ಪ್ರಮಾಣೀಕೃತ ಕಾರ್ಯಾಚರಣೆ ಪ್ರಕ್ರಿಯೆ ಎಸ್ಒಪಿ ಉಲ್ಲೇಖಿಸಿರುವ ರೈಲ್ವೇಸ್ ರೈಲಿನ ಪ್ರವೇಶ ಪ್ರಯಾಣದ ಅವಧಿಯಲ್ಲಿ ಪ್ರಯಾಣಿಕರೂ ಮಾಸ್ಕ್ಗಳನ್ನು ಧರಿಸಿರುವುದು ಕಡ್ಡಾಯವಾಗಿರಲಿದೆ ಎಂದು ಸೂಚಿಸಿದೆಕೋವಿಡ್ ಸೋಂಕಿನ ಹೆಚ್ಚಳದ ನಡುವೆ ಭಾರತೀಯ ರೈಲುಗಳ ಸಂಚಾರದ ಸಂಬಂಧ ರ ಮೇ ರಂದು ಎಸ್ಒಪಿ ಜಾರಿಗೆ ತರಲಾಗಿತ್ತು ಅದರಲ್ಲಿ ಪ್ರಯಾಣಿಕರು ರೈಲು ಪ್ರವೇಶಿಸುವುದರಿಂದ ಪ್ರಯಾಣದುದ್ದಕ್ಕೂ ನಂತರ ಮಾಸ್ಕ್ ಧರಿಸುವುದು ಕಡ್ಡಾಯ ಎಂದು ಹೇಳಲಾಗಿತ್ತು ಮಾಸ್ಕ್ ಧರಿಸದೆ ಇರುವುದು ರೈಲ್ವೆ ಕಾಯ್ದೆಯಡಿ ಅಪರಾಧ ಎಂದು ತಿಳಿಸಿದೆನಾಸಾದ ಚಂದ್ರಯಾನ ಯೋಜನೆ ಬಿಡ್ ಗೆದ್ದ ಎಲೋನ್ ಮಸ್ಕ್ಚಂದ್ರನ ಅಂಗಳದಲ್ಲಿ ಗಗನಯಾತ್ರಿಕರನ್ನು ಇಳಿಸುವ ನಾಸಾದ ಬೃಹತ್ ಯೋಜನೆಯ ಗುತ್ತಿಗೆಯನ್ನು ಖ್ಯಾತ ಉದ್ಯಮಿ ಎಲೋನ್ ಮಸ್ಕ್ ಪಡೆದುಕೊಂಡಿದ್ದಾರೆ ರ ಆರಂಭದಲ್ಲಿ ಚಂದ್ರನಲ್ಲಿಗೆ ಗಗನಯಾತ್ರಿಕರನ್ನು ತರುವ ಬಾಹ್ಯಾಕಾಶ ನೌಕೆಯನ್ನು ರೂಪಿಸುವ  ಬಿಲಿಯಲ್ ಡಾಲರ್ ಮೌಲ್ಯದ ಗುತ್ತಿಗೆಯನ್ನು ಎಲೋನ್ ಮಸ್ಕ್ ಒಡೆತನದ ಖಾಸಗಿ ಬಾಹ್ಯಾಕಾಶ ಸಂಸ್ಥೆ ಸ್ಪೇಸ್ಎಕ್ಸ್ ಪಡೆದುಕೊಂಡಿದೆ ಕ್ರಮವಾಗಿ ಜಗತ್ತಿನ ಮೊದಲ ಮೂರನೇ ಅತ್ಯಂತ ಶ್ರೀಮಂತ ವ್ಯಕ್ತಿಗಳು ಎನಿಸಿರುವ ಬೆಜೋಸ್ ಮಸ್ಕ್ ನಾಸಾದ ಮಹತ್ವಾಕಾಂಕ್ಷಿ ಯೋಜನೆಯ ಬಿಡ್ ಪಡೆಯಲು ತೀವ್ರ ಪೈಪೋಟಿ ನಡೆಸಿದ್ದರು ರ ಚಂದ್ರನಲ್ಲಿ ಮೊದಲ ಬಾರಿ ಮನುಷ್ಯರನ್ನು ಇಳಿಸುವ ಬೃಹತ್ ಯೋಜನೆಯು ಯಾರ ಪಾಲಾಗಿದೆ ಎನ್ನುವುದು ಕುತೂಹಲ ಮೂಡಿಸಿತ್ತುಮಸ್ಕ್ ಸ್ಪೇಕ್ಸ್ ಎಕ್ಸ್ ಏಕಾಂಗಿಯಾಗಿ ಬಿಡ್ ಸಲ್ಲಿಸಿದರೆ ಅಮೇಜಾನ್ಕಾಂನ ಸಂಸ್ಥಾಪಕ ಬೆಜೋಸ್ ಬ್ಲೂ ಒರಿಜಿನ್ ಸಂಸ್ಥೆಯು ಲಾಖ್ಹೀಡ್ ಮಾರ್ಟಿಕ್ ಕಾರ್ಪ್ ನಾರ್ತ್ರೋಪ್ ಗ್ರುಮ್ಮನ್ ಕಾರ್ಪ್ ಡ್ರೇಪರ್ ಸಹಭಾಗಿತ್ವದಲ್ಲಿ ಬಿಡ್ಡಿಂಗ್ ನಡೆಸಿತ್ತು ಜತೆಗೆ ರಕ್ಷಣಾ ಗುತ್ತಿಗೆದಾರ ಕಂಪೆನಿ ಡೈನೆಟಿಕ್ಸ್ ಸ್ಪರ್ಧೆಯಲ್ಲಿತ್ತುಆದರೆ ಗುತ್ತಿಗೆಯನ್ನು ಬಾಹ್ಯಾಕಾಶ ಕ್ಷೇತ್ರದಲ್ಲಿ ಗಮನಾರ್ಹ ಸಾಧನೆ ತೋರಿಸುತ್ತಿರುವ ಸ್ಪೇಸ್ ಎಕ್ಸ್ಗೆ ನಾಸಾ ಘೋಷಣೆಯ ನಾಸಾ ರೂಲ್ಸ್ ಎಂದು ಎಲೋನ್ ಮಸ್ಕ್ ಟ್ವೀಟ್ ಮಾಡಿದ್ದಾರೆ ಆರ್ಟೆಮಿಸ್ ಕಾರ್ಯಕ್ರಮದ ಭಾಗವಾಗಿ ಅಮೆರಿಕದ ಬಾಹ್ಯಾಕಾಶ ಸಂಸ್ಥೆಯು ಮೊದಲ ವಾಣಿಜ್ಯ ಹ್ಯೂಮನ್ ಲ್ಯಾಂಡರ್ ಯೋಜನೆಯ ಒಪ್ಪಂದ ಮಾಡಿಕೊಂಡಿದೆನಾಸಾವು ಅಪೋಲೋ ಯೋಜನೆಯ ನಡೆಯುತ್ತಿರುವ ಮೊದಲ ಮನುಷ್ಯರನ್ನು ಚಂದ್ರನ ಇಳಿಸುವ ಚಂದ್ರಯಾನ ಯೋಜನೆಗೆ ಸ್ಟಾರ್ಶಿಪ್ಅನ್ನು ಆಯ್ಕೆಮಾಡಿಕೊಂಡಿದೆ ಮಾನವ ಬಾಹ್ಯಾಕಾಶ ಆವಿಷ್ಕಾರದಲ್ಲಿನ ಹೊಸ ಯುಗದಲ್ಲಿ ನಾಸಾಗೆ ಸಹಾಯ ಮಾಡಲು ವಿಧೇಯರಾಗಿದ್ದೇವೆ ಎಂದು ಸ್ಪೇಸ್ ಎಕ್ಸ್ ಟ್ವೀಟ್ ಮಾಡಿದೆ";

export const text2 =
  "ಹೊಟ್ಟೆಎಲ್ಲಾರು ಮಾಡುವುದು ಹೊಟ್ಟೆಗಾಗಿ ತುಂಡು ಬಟ್ಟೆಗಾಗಿ ಅನ್ನೋ ಪುರಂದರದಾಸರ ಕೀರ್ತನೆ ನೆನಪಾಗುತ್ತಿತ್ತು ಕಾರಣ ನಿನ್ನೆ ರಾತ್ರೆ ಗೆಳೆಯನ ಮನೆಗೆ ಹೋಗಿ ಅವನ ಅನಿರೀಕ್ಷಿತ ಒತ್ತಾಯಕ್ಕೆ ಮಣಿದು ಹತ್ತಿದ ನಳಪಾಕಕ್ಕೂ ಬಯ್ಯಲಾರದೇ ಹೋದ ಅರ್ಧ ತುಂಬಿದ ಹೊಟ್ಟೆಯೋ ಇಂದು ಬೆಳಗ್ಗೆಯ ಗಡಿಬಿಡಿಯ ತಿಂಡಿಯೋ ಮಧ್ಯಾಹ್ನದ ಕಾರ್ಯಕ್ರಮದಲ್ಲಿ ದಕ್ಕಿದ ಅರೆಹೊಟ್ಟೆ ಮೊಸರನ್ನವೋ ಗೊತ್ತಿಲ್ಲ ಮಧ್ಯಾಹ್ನ ಹನ್ನೆರಡಾಗೋವಷ್ಟರಲ್ಲೇ ಸಾರುತ್ತಿದ್ದ ಹೊಟ್ಟೆ ಟ್ರಿಪ್ಪು ಟ್ರಿಪ್ಪೆಂದು ಹುಚ್ಚನಂತೆ ಅಲೆಯುತ್ತಿದ್ದಾಗ ಅಲೆಮಾರಿಯ ಸಾಥಿ ಮರಿಯಲೆಮಾರಿಯಾಗಿ ಮೂರೂವರೆಯಾದರೂ ಇರುವಿಕೆಯನ್ನೇ ಮರೆತುಬಿಡುತ್ತಿತ್ತುನಮ್ಮ ಕಡೆಯೊಂದು ಮಾತಿದೆ ಹಡಗು ತುಂಬೋಕೆ ಹೋದವನು ಬಂದನಂತೆಹೊಟ್ಟೆ ತುಂಬೋಕೆ ಬರಲಿಲ್ಲವಂತೆ ಹೊಟ್ಟೆಯೆಂದಾಕ್ಷಣ ಪ್ರತೀ ದಿನ ಬಂಡಿ ಅನ್ನ ಒಬ್ಬ ಮನುಷ್ಯನನ್ನು ತಿಂದು ತೇಗುತ್ತಿದ್ದ ಬಕಾಸುರ ಅವನ ಇಡೀ ಬಂಡಿ ಅನ್ನವನ್ನು ತಿಂದು ಅವನನ್ನೂ ಕೊಂದ ಭೀಮಸೇನ ಆರು ತಿಂಗಳು ಮಲಗಿ ಎದ್ದ ನಂತರ ಬಂಡಿಗಟ್ಟಲೇ ಅನ್ನ ತಿನ್ನುತ್ತಿದ್ದ ಕುಂಭಕರ್ಣ ಹೀಗೆ ಪ್ರಸಂಗಗಳು ನೆನಪಾಗುತ್ತೆ ಅಷ್ಟೆಲ್ಲಾ ಹಿಂದೆ ಹೋಗೋದೇಕೆ ಜನರ ಅನ್ನವನ್ನು ತಿಂದು ಹೊಟ್ಟೆ ತುಂಬದೇ ದನಗಳ ಕೋಟಿ ಕೋಟಿ ರೂ ಮೇವನ್ನೂ ತಿಂದು ನೀರು ಕುಡಿದ ಪುಣ್ಯಾತ್ಮರು ಬಡವರು ರಕ್ತ ಹೀರಿ ಸಂಪಾದಿಸಿದ ದುಡ್ಡನ್ನು ಕಸಿಯೋ ಸಾಮಾನ್ಯರ ತಲೆಯ ಹೊರೆಗಟ್ಟಲೇ ತೆರಿಗೆ ಹೊದಿಸಿದ ದುಡ್ಡನ್ನು ನುಂಗೋದಲ್ಲದೇ ರಸ್ತೆ ಲೈಟು ನೀರಂತಹ ಸೌಲಭ್ಯಗಳಿಗೆ ಮಂಜೂರು ಮಾಡಿರೋ ದುಡ್ಡನ್ನೂ ಗುಳುಂಮೆನಿಸೋ ಮಹಾಮಹಿಮರ ಬೆಲ್ಲಿ ಡ್ಯಾನ್ಸ್ ನೆನಪಾಗುತ್ತದೆ ಹೊತ್ತು ತುತ್ತಿಗೂ ಗತಿಯಿಲ್ಲದೇ ರಟ್ಟೆ ಸುರಿಸಿ ಕೂಲಿ ಕೆಲಸ ತಾವು ಮಾಡ್ತಿರೋದು ಹೊಟ್ಟೆಗಾಗೇ ಎನ್ನೋದು ಸರಿ ಮಹಾಮಹಿಮರೂ ಅಂದರೆ ಕೂತು ತಿಂದರೂ ಏಳು ತಲೆಮಾರಿನ ಊಟಕ್ಕೆ ತೊಂದರೆಯಿಲ್ಲವೆಂದು ಹೊಟ್ಟೆಗೆ ಹೋಲಿಸಿಯೇ ಹೇಳುವಾಗ ಮಹಾಮಹಿಮರ ಮಾತು ಯಾವ ರೀತಿಯಲ್ಲಿ ಸತ್ಯವೆಂಬುದು ಅರ್ಥವಾಗುತ್ತದೆ ಬಡವರು ಬರದಲ್ಲಿ ಚಳಿಯಲ್ಲಿ ಹೊಟ್ಟೆಗಿಲ್ಲದೇ ಸಾಯುತ್ತಿದ್ದರೂ ಫೈವ್ ಸ್ಟಾರ್ ಪಾರ್ಟಿ ಮಾಡೋ ಭೋಜನಕೂಟ ಮಾಡೋ ಮಹಿಮರನ್ನು ಅಪೌಷ್ಟಿಕತೆಯಿಂದ ಹೊಟ್ಟೆ ಬೆನ್ನಿಗಂಟಿರುವಂತೆ ಕಾಣುವ ಬಡ ಮಕ್ಕಳು ಒಂದೆಡೆಯಾದರೆ ದಿನಾ ತಟ್ಟೆಯಲ್ಲಿ ಅರ್ಧಕ್ಕಿಂತ ಹೆಚ್ಚು ಅನ್ನ ಚೆಲ್ಲಿ ಹೊಟ್ಟೆ ಬಂತೆಂದು ಜಿಮ್ಮಿಗೆ ಹೋಗೋ ಪುಣ್ಯಾತ್ಮರನ್ನು ಹೀಗೆ ವಿವಿಧ ರೀತಿಯ ಹೊಟ್ಟೆಯ ವೈರುಧ್ಯಗಳನ್ನು ತೆಗಳುತ್ತಾ ಹೋದರೆ ಬೇಕಾದಷ್ಟಾಗುತ್ತದೆ ಪುಟಗಟ್ಟಲೆ ಬರೆದ ಸಾಲುಗಳೂ ಭಾವಗಳೂ ರಾವಣನ ಹೊಟ್ಟೆಗೆ ಅರೆಕಾಸು ಮಜ್ಜಿಗೆಯಂತೆ ಗೌಣವಾಗುತ್ತದೆ ದೊಡ್ಡಣ್ಣನಂತೆ ಹೊಟ್ಟೆ ಹುಣ್ಣಾಗಿಸುವಂತೆ ನಗಿಸೋದು ಸಾಧ್ಯವಾಗದಿದ್ದರೂ ಜ್ವಲಂತ ಸಮಸ್ಯೆಗಳ ಬರೆದು ಇನ್ಯಾವುದೋ ದೇಶದ ಒಳ್ಳೆಯತನವನ್ನು ನಮ್ಮಲ್ಲಿನ ದಾರಿದ್ರ್ಯವನ್ನು ತೆಗಳಿ ಹೊಟ್ಟೆ ಉರಿಸದೇ ಇರುವುದು ಮೇಲನಿಸುತ್ತೆಂಬ ಧೃಢ ನಂಬಿಕೆಯೇ ಒಂದಿಷ್ಟು ಮಾತು ಹರಟೆಗೆ ದನಿಯಾಗಿದೆ ಇಂದು";

